import {
  // camelizeKeys,
  // hyphenateKeys,
  extend,
  extendTruthy
  // trimComment,
  // normalizeStyle,
  // autoPrefix,
  // isArray,
  // getParentScroller,
  // supportSticky,
  // appendCss
} from '../utils'

function getInlineStyle (vnode) {
  const data = vnode.data || {}
  return extendTruthy({}, data.staticStyle, data.style)
}

export function extractComponentStyle (context) {
  return getComponentInlineStyle(context)
  // return getComponentStyle(context, true)
}

export function getComponentInlineStyle (context) {
  let vnode = context && context.$vnode
  if (!vnode) {
    if (process.env.NODE_ENV === 'development') {
      return console.error('[vue-render] getComponentStyle failed: no $vnode in context.')
    }
    return {}
  }
  const style = {}
  while (vnode) {
    extend(style, getInlineStyle(vnode))
    vnode = vnode.parent
  }
  return style
}




