/**
 * Navigator module
 */
const navigatorModule = {
  push: function (config, callback) {
    window.location.href = config.url
    callback && callback()
  },

  pop: function (config, callback) {
    window.history.back()
    callback && callback()
  }
}

export default {
  init (weex) {
    weex.registerModule('navigator', navigatorModule)
  }
}



